import * as Tone from 'tone';
import React, { useState } from 'react';
import { SongIDs } from './AudioEngine/Constants';

function App() {

  //AUDIO ENGINE
  const [audioEngine, setAudioEngine] = useState(null);

  const initEngine = async (tone) => {

    if (!audioEngine) {
      
      console.log("APP: Init ToneJS and Audio Engine");
      
      //create context for playback, which is not that interactive / responsive, but easier on the CPU
      const context = new Tone.Context({ latencyHint: "playback" });
      Tone.setContext(context);
      console.log("AUDIO: setContext, latencyHint:", Tone.getContext().latencyHint); //double check latencyHint
      
      //prevents clicking sound with Tone starts up below
      Tone.getContext().rawContext.suspend();

      // Start up tone
      await Tone.start();
      console.log("AUDIO: Tone started", Tone.getContext().state, Tone);

      //import AudioEngine class (we can't do this before tone is init'd)
      const { default: AudioEngine } = await import('./AudioEngine/AudioEngine');
      
      //pass tone reference into audio engine on init
      const engine = new AudioEngine(tone);
      setAudioEngine(engine);
      return engine;
    }
    return audioEngine;
  };

  

  // pass in brainwave ID to play song
  const playSong = async (songID) => {

    
    const engine = await initEngine(Tone);
    if (engine) {
      engine.playSong(songID);
    } else {
      console.warn("APP: Audio engine not initialized yet");
    }
  };
  
  // stopp all elements of the song
  // const stopSong = async () => {
  //   const engine = await initEngine();
  //   if (engine) {
  //     engine.stopSong();
  //   } else {
  //     console.warn("APP: Audio engine not initialized yet");
  //   }
  // };

  return (
    <div>
      <h1>H5 Audio Engine</h1>
      <button onClick={() => playSong(SongIDs.INTRO)}>Intro</button>
      <br></br>
      <button onClick={() => playSong(SongIDs.GAMMA)}>Gamma</button>
      <button onClick={() => playSong(SongIDs.BETA)}>Beta</button>
      <button onClick={() => playSong(SongIDs.ALPHA)}>Alpha</button>
      <button onClick={() => playSong(SongIDs.THETA)}>Theta</button>
      <button onClick={() => playSong(SongIDs.DELTA)}>Delta</button>
      <br></br>
      {/* <button onClick={stopSong}>Stop</button> */}
    </div>
  );
}

export default App;